<template>
	<q-page padding>
		<div class="q-py-sm">
			<q-btn
				round
				color="primary"
				icon="arrow_back"
				to="/"
			></q-btn>
		</div>
		<div class="q-pt-sm">
			<q-card>
				<image-casrouse v-show="isContainPhotos"/>
				<q-card-section>
					<q-btn
						round
						color="primary"
						icon="place"
						size="md"
						class="absolute"
						style="top: 0; right: 12px; transform: translateY(-50%);"
						@click="openLink"
					></q-btn>
					<div class="row no-wrap items-center">
						<div class="col text-h6 ellipsis">
							{{ name }}
						</div>
					</div>
					<div style="display: inline-flex">
						<q-rating
							v-model="stars"
							readonly
							:max="5"
							color="positive"
							size="32px"
						></q-rating>
						<div class="text-subtitle1 q-mt-sm q-ml-md">
							{{stars}}/{{ratingTotal}}
							<span class="text-caption"> 人評論</span>
						</div>
					</div>
					<div class="row">
						<div class="col">
							<q-icon name="home" class="text-teal"></q-icon>
							<span v-html="address" class="q-ml-sm"></span>
						</div>
					</div>
					<div class="row">
						<div class="col">
							<q-icon name="store_mall_directory" class="text-orange"></q-icon>
							<span class="q-ml-sm">{{ isOpening }}</span>
						</div>
					</div>
					<div class="row">
						<div class="col">
							<q-icon name="phone" class="text-info"></q-icon>
							<a :href="'tel: ' + phone" class="q-ml-sm">{{phone}}</a>
						</div>
					</div>
					<div class="row" v-if="webUrl">
						<div class="col">
							<q-icon name="web" class="text-info"></q-icon>
							<a :href="webUrl" class="q-ml-sm" target="_blank">
								檢視網頁<q-icon name="open_in_new"></q-icon>
							</a>
						</div>
					</div>
				</q-card-section>
				<q-card-actions>
					<q-space></q-space>
					看評論
					<q-btn
						color="grey"
						round
						flat
						dense
						:icon="expanded ? 'keyboard_arrow_up': 'keyboard_arrow_down'"
						@click="expanded = !expanded"
					></q-btn>
				</q-card-actions>
				<q-slide-transition>
					<div v-show="expanded">
						<q-separator></q-separator>
						<q-card-section
							v-for="(review, index) in getReviews"
							:key="index"
						>
							<div class="text-subtitle1">
								{{ review.author_name }}
								<span class="float-right">
									{{ review.relative_time_description }}
									<q-rating
										v-model="review.rating"
										readonly
										color="positive"
										:max="5"
									></q-rating>
								</span>
							</div>
							<div class="text-caption text-grey">
								{{ review.text }}
							</div>
						</q-card-section>
					</div>
				</q-slide-transition>
			</q-card>
		</div>
	</q-page>
</template>

<script>
import { mapGetters } from 'vuex';
import ImageCasrouse from '@/components/ImageCasrouse.vue';

export default {
	name: 'Restaurant',
	components: {
		ImageCasrouse,
	},
	data() {
		return {
			stars: 0,
			name: '',
			address: '',
			isOpening: '',
			expanded: false,
			url: '',
			phone: '',
			ratingTotal: 0,
			webUrl: '',
			isContainPhotos: true,
		};
	},
	mounted() {
		const routeObject = this.$router.currentRoute;
		this.$store.dispatch('fetchSelectedRestaurant', routeObject.params.id);
		this.$store.commit('setSelectedRestaurantPhotos', []);
	},
	computed: {
		...mapGetters(['getSelectedRestaurant']),
		getReviews() {
			let reviews = [];
			if (this.getSelectedRestaurant && Array.isArray(this.getSelectedRestaurant.reviews)) {
				reviews = Object.assign([], this.getSelectedRestaurant.reviews);
				reviews.sort((a, b) => {
					return a.time > b.time ? -1 : 1;
				});
			}
			return reviews;
		},
	},
	watch: {
		getSelectedRestaurant() {
			if (this.getSelectedRestaurant) {
				this.stars = this.getSelectedRestaurant.rating
					? this.getSelectedRestaurant.rating
					: 0;
				this.name = this.getSelectedRestaurant.name;
				this.address = this.getSelectedRestaurant.adr_address;
				this.phone = this.getSelectedRestaurant.formatted_phone_number;
				if (this.getSelectedRestaurant.opening_hours) {
					this.isOpening = this.getSelectedRestaurant.opening_hours
						.open_now ? '營業中' : '已收工';
				} else {
					this.isOpening = '未知的營業時間';
				}
				this.url = this.getSelectedRestaurant.url;
				this.ratingTotal = this.getSelectedRestaurant.user_ratings_total;
				this.webUrl = this.getSelectedRestaurant.website
					? this.getSelectedRestaurant.website : null;
				const photosReference = [];
				const { photos } = this.getSelectedRestaurant;
				if (photos) {
					for (let i = 0; i < 5; i += 1) {
						photosReference.push(photos[i].photo_reference);
					}
				} else {
					this.isContainPhotos = false;
				}
				const payload = {
					photosReference,
					maxheight: 400,
				};
				this.$store.dispatch('fetchPhotos', payload);
			}
		},
	},
	methods: {
		openLink() {
			window.open(this.url, '_blank');
		},
	},
};
</script>
