<template>
	<div>
		<q-skeleton height="300px" square v-if="photos.length === 0"></q-skeleton>
		<q-carousel swipeable animated v-model="slide" thumbnails infinite height="300px" v-else>
			<q-carousel-slide
				v-for="(photo, index) in photos"
				:key="index"
				:name="index"
				:img-src="photo"
			></q-carousel-slide>
		</q-carousel>
	</div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
	name: 'ImageCasrouse',
	data() {
		return {
			slide: 0,
		};
	},
	computed: {
		...mapGetters(['getSelectedRestaurantPhotos']),
		photos() {
			return this.getSelectedRestaurantPhotos ? this.getSelectedRestaurantPhotos : [];
		},
	},
};
</script>
